<template>
  <el-form :model="essayForm" :rules="rules" ref="essayForm" style="width: 80%" class="demo-ruleForm" >
    <el-form-item label="文章标题" prop="checkTitle">
      <el-input v-model="essayForm.title"  autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="文章描述" prpo="checkDesc">
      <el-input v-model="essayForm.description"  autocomplete="off" ></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('essayForm')">提交</el-button>
      <el-button @click="resetForm('essayForm')">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    var checkTitle= (rule, value, callback) =>{
      if (value === '') {
        callback(new Error('请输入文章标题'));
      }
    };
    var checkDesc = (rule, value, callback) =>{
      if (value === '') {
        callback(new Error('请输入文章描述'));
      } else if (value.size >20) {
        callback(new Error('请限制在20字以内'));
      } else {
        callback();
      }
    };
    return {
      essayForm: {
        title: '',
        description: '',
      },
      rules: {
        checkTitle: [
          { validator: checkTitle, trigger: 'blur' }
        ],
        checkDesc: [
          { validator: checkDesc, trigger: 'blur' }
        ],
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

